<template>
	<el-dialog title="" :visible.sync="imageModel" width="80%" top="5vh">
		
		<el-container style="position: relative;height: 70vh;margin: -30px -20px;">
		  <el-header class="d-flex align-items-center border-bottom">
			  <!--  -->
			  <div class="d-flex mr-auto">
				  <el-select class="mr-2" placeholder="" 
				  size="mini" v-model="searchForm.order"
				  style="width: 150px;">
				  	<el-option label="" value="desc"></el-option>
				  	<el-option label="" value="asc"></el-option>
				  </el-select>
				  <el-input class="mr-2" size="mini" 
				  placeholder="" v-model="searchForm.keyword"
				  style="width: 150px;"></el-input>
				  <el-button type="success" size="mini"
				  @click="getImageList"></el-button>
			  </div>
			  
		  </el-header>
		  <el-container>
			<el-aside width="200px" style="position: absolute;top: 60px;left: 0;bottom: 60px;" class="bg-white border-right" v-loading="asideLoading">
				<!--  | -->
				<ul class="list-group list-group-flush">
					<album-item v-for="(item,index) in albums" :key="index"
					:item="item" :index="index" 
					:active="albumIndex === index"
					@change="albumChange"
					:showOptions="false"></album-item>
				</ul>
				
			</el-aside>
			<el-container>
			  <el-main style="position: absolute;top: 60px;left:200px;bottom: 60px;right: 0;"
			  v-loading="mainLoading">
				  <!--  -->
				  <el-row :gutter="10">
				  	<el-col :span="24" :lg="4" :md="6" :sm="8"
					v-for="(item,index) in imageList" :key="index">
						<el-card class="box-card mb-3 position-relative" 
						style="cursor: pointer;"
						:body-style="{'padding':'0'}" shadow="hover">
						
						<div class="border" 
						:class="{'border-danger':item.ischeck}">
							
							<span class="badge badge-danger"
							style="position: absolute;right: 0;top: 0;"
							v-if="item.ischeck">
							{{item.checkOrder}}</span>
						
							
							<img :src="item.url" class="w-100" 
							style="height: 100px;" 
							@click="choose(item)"/>
							<div class="w-100 text-white px-1" style="background: rgba(0,0,0,0.5);margin-top: -25px;position: absolute;">
								<span class="small">{{item.name}}</span>
							</div>
							
							<div class="p-2 text-center">
								<el-button-group>
								  <el-button icon="el-icon-edit" size="mini" class="p-2" @click="imageEdit(item,index)"></el-button>
								  <el-button icon="el-icon-delete" size="mini" class="p-2" @click="imageDel({index})"></el-button>
								</el-button-group>
							</div>
							
						</div>
						
						
						</el-card>
					</el-col>
				  </el-row>
				 
			  </el-main>
			</el-container>
		  </el-container>
		  <el-footer class="border-top d-flex align-items-center px-0">
			  <!--  -->
			  <div style="width: 200px; flex-shrink: 0;" class="h-100 d-flex align-items-center justify-content-center border-right">
				  <el-button-group>
					  <el-button size="mini" :disabled="albumPage === 1" @click="changeAlbumPage('pre')"></el-button>
					  <el-button size="mini" @click="changeAlbumPage('next')" :disabled="albumPage === Math.ceil(albumTotal / 10)"></el-button>
				  </el-button-group>
			  </div>
			  <div style="flex: 1;" class="px-2">
				  <el-pagination
				    @size-change="handleSizeChange"
				    @current-change="handleCurrentChange"
				    :current-page="currentPage"
				    :page-sizes="pageSizes"
				    :page-size="pageSize"
				    layout="total, sizes, prev, pager, next, jumper"
				    :total="total">
				  </el-pagination>
			  </div>
		  </el-footer>
		</el-container>
		
		
		
		<div slot="footer" class="dialog-footer">
			<el-button @click="hide"> </el-button>
			<el-button type="primary" @click="confirm"> </el-button>
		</div>
	</el-dialog>
</template>

<script>
	import albumItem from '@/components/image/album-item.vue';
	export default {
		props: {
			// 
			max: {
				type: Number,
				default: 9
			},
		},
		components: {
		  albumItem
		},
		data() {
			return {
				imageModel:false,
				callback:false,
		
				searchForm:{
					order:"",
					keyword:""
				},
				albumIndex:0,
				albums:[],
				albumPage:1,
				albumTotal:0,
				
				
				imageList:[],
				// 
				chooseList:[],
				currentPage:1,
				pageSize:10,
				pageSizes:[10,20,50,100],
				total:10,
				
				asideLoading:false,
				mainLoading:false
			}
		},
		computed: {
			// id
			image_class_id(){
				let item = this.albums[this.albumIndex]
				if(item){
					return item.id
				}
				return 0
			},
			// URL
			getImageListUrl(){
				let other = ''
				if(this.searchForm.keyword != ''){
					other = `&keyword=${this.searchForm.keyword}`
				}
				return `/admin/imageclass/${this.image_class_id}/image/${this.currentPage}?limit=${this.pageSize}&order=${this.searchForm.order}${other}`
			}
		},
		methods: {
			// 
			changeAlbumPage(type){
				if(type === 'pre'){
					this.albumPage--
				} else {
					this.albumPage++
				}
				this.__init()
			},
			// 
			chooseImage(callback){
				// 
				this.__init()
				this.callback = callback
				this.imageModel = true
				this.unChoose()
			},
			// 
			confirm(){
				// url
				if (typeof this.callback === 'function') {
					this.callback(this.chooseList)
				}
				// 
				this.hide()
			},
			// 
			hide(){
				this.imageModel = false
				this.callback = false
			},
			
			
			// 
			unChoose(){
				this.imageList.forEach(img=>{
					// 
					let i = this.chooseList.findIndex(item=>{
						return item.id === img.id
					})
					if (i > -1) {
						// 
						img.ischeck = false
						img.checkOrder = 0
						// chooseList
						this.chooseList.splice(i,1)
					}
				})
			},
			// 
			choose(item){
				// 
				if (!item.ischeck) {
					// 
					if (this.chooseList.length >= this.max) {
						return this.$message({
							message: ''+this.max+'',
							type: 'warning'
						});
					}
					// 
					this.chooseList.push({ id:item.id,url:item.url})
					// 
					item.checkOrder = this.chooseList.length
					// 
					item.ischeck = true
					return;
				}
				// 
				// chooseList
				let i = this.chooseList.findIndex(v=>v.id === item.id)
				if(i===-1) return;
				// 
				let length = this.chooseList.length
				// 
				if (i+1 < length) {
					// imageList
					for (let j = i; j < length; j++) {
						let no = this.imageList.findIndex(v=>v.id === this.chooseList[j].id)
						if (no > -1) {
							this.imageList[no].checkOrder--
						}
					}
				}
				// 
				this.chooseList.splice(i,1)
				// 
				item.ischeck = false
				// 
				item.checkOrder = 0
			},
			__init() {
				// 
				this.asideLoading = true
				this.axios.get('/admin/imageclass/'+this.albumPage,{
					token:true
				}).then(res=>{
					let result = res.data.data
					this.albums = result.list
					this.albumTotal = result.totalCount
					this.asideLoading = false
					// 
					this.getImageList()
				}).catch(err=>{
					this.asideLoading = false
				})
			},
			// 
			getImageList(){
				this.mainLoading = true
				this.axios.get(this.getImageListUrl,{
					token:true
				}).then(res=>{
					let result = res.data.data
					this.imageList = result.list.map(item=>{
						return { 
							id:item.id,
							url:item.url,
							name:item.name,
							ischeck:false,
							checkOrder:0
						}
					})
					this.total = result.totalCount
					this.mainLoading = false
				}).catch(err=>{
					this.mainLoading = false
				})
			},
			// 
			albumChange(index){
				this.albumIndex = index
				this.getImageList()
			},
			// 
			imageEdit(item,index){
				this.$prompt('', '', {
				  confirmButtonText: '',
				  cancelButtonText: '',
				  inputValue:item.name,
				  inputValidator(val){
					  if (val === '') {
					  	return ''
					  }
				  }
				}).then(({ value }) => {
					item.name = value
					this.$message({
						message: '',
						type: 'success'
					});
				})
			},
			// 
			imageDel(obj){
				this.$confirm(obj.all?'?':'?',
				'', {
					confirmButtonText: '',
					cancelButtonText: '',
					type: 'warning'
				}).then(() => {
					if (obj.all) {
						let list = this.imageList.filter(img=>{
							return !this.chooseList.some(c=>c.id === img.id)
						})
						this.imageList = list
						this.chooseList = []
					}else{
						this.imageList.splice(obj.index,1)
					}
					this.$message({
						message: '',
						type: 'success'
					});
				})
			},
			handleSizeChange(val) {
				console.log(` ${val} `);
			},
			handleCurrentChange(val) {
				console.log(`: ${val}`);
			}
		},
	}
</script>

