export default {
	data(){
		return {
			preUrl:"",
			
			page:{
				current:1,
				sizes:[10, 20, 50, 100],
				size:10,
				total:0
			},
			
			multipleSelection: [],
			
			loading:true,
		}
	},
	filters: {
		numToString(value) {
			return value.toString();
		}
	},
	created(){
		this.getList()
	},
	computed: {
		// id
		ids() {
			return this.multipleSelection.map(item=>{
				return item.id
			}) 
		}
	},
	methods:{
		showLoading(){
			if(this.loading){
				this.layout.showLoading()
			}
		},
		hideLoading(){
			if(this.loading){
				this.layout.hideLoading()
			}
		},
		// 
		getListResult(data){
			
		},
		// url
		getListUrl(){
			return `/admin/${this.preUrl}/${this.page.current}?limit=${this.page.size}`
		},
		// 
		getList(){
			if(this.preUrl == '') return;
			this.showLoading()
			let url = this.getListUrl();
			this.axios.get(url,{
				token:true
			}).then(res=>{
				let data = res.data.data
				this.page.total = data.totalCount
				
				this.getListResult(data)

				this.hideLoading()
			}).catch(err=>{
				this.hideLoading()
			})
		},
		// /
		addOrEdit(data,id = 0){
			this.showLoading()
			let msg = id > 0 ? '' : ''
			let url = id > 0 ? '/admin/'+this.preUrl+'/'+id : '/admin/'+this.preUrl
			this.axios.post(url,data,{
				token:true
			}).then(res=>{
				this.$message({
					message: msg + '',
					type: 'success'
				});
				this.getList()
				this.hideLoading()
			}).catch(err=>{
				this.hideLoading()
			})
		},
		// url
		deleteAllUrl(){
			return '/admin/'+this.preUrl+'/delete_all'
		},
		// 
		deleteAll(){
			if (this.ids.length === 0) {
				return this.$message({
					message: '',
					type: 'error'
				});
			}
			this.$confirm('?', '', {
				confirmButtonText: '',
				cancelButtonText: '',
				type: 'warning'
			}).then(() => {
				this.showLoading()
				let url = this.deleteAllUrl()
				this.axios.post(url,{
					ids:this.ids
				},{
					token:true
				}).then(res=>{
					this.multipleSelection = []
					this.$message({
						message: '',
						type: 'success'
					});
					this.getList()
					this.hideLoading()
				}).catch(err=>{
					this.hideLoading()
				})
			})
		},
		// 
		changeStatus(item){
			let status = item.status === 1 ? 0 : 1
			let msg = status === 1 ? '' : ''
			this.showLoading()
			this.axios.post('/admin/'+this.preUrl+'/'+item.id+'/update_status',{
				status:status
			},{
				token:true
			}).then(res=>{
				item.status = status
				this.$message({
					message: msg + '',
					type: 'success'
				});
				this.hideLoading()
			}).catch(err=>{
				this.hideLoading()
			})
		},
		// 
		deleteItem(item){
			this.$confirm('?', '', {
				confirmButtonText: '',
				cancelButtonText: '',
				type: 'warning'
			}).then(() => {
				this.showLoading()
				this.axios.post('/admin/'+this.preUrl+'/'+item.id+'/delete',{},{
					token:true
				}).then(res=>{
					this.$message({
						message: '',
						type: 'success'
					});
					this.getList()
					this.hideLoading()
				}).catch(err=>{
					this.hideLoading()
				})
			})
		},
		// 
		handleSelectionChange(val) {
			this.multipleSelection = val;
		},
		handleSizeChange(val) {
			this.page.size = val
			this.getList()
		},
		handleCurrentChange(val) {
			this.page.current = val
			this.getList()
		}
	}
}