/**
 * 
 * index/indexshop/indexindexpathnameindex
 * shop/listnameshop_listindexshop/indexshop
 * 
 * **/
let routes = [
  {
    path: "/",
    name: "layout",
    redirect: { name: "index" },
    component: "layout",
    children: [
      {
        meta: { title: "" },
        component: "index/index",
      },
      {
        meta: { title: "" },
        component: "shop/goods/list",
      },
      {
        meta: { title: "" },
        component: "shop/goods/create",
      },
      {
        meta: { title: "" },
        component: "shop/goods/content",
      },
      {
        meta: { title: "" },
        component: "shop/goods/banner",
      },
      {
        meta: { title: "" },
        component: "shop/goods/attr",
      },
      {
        meta: { title: "" },
        component: "shop/goods/sku",
      },
      {
        meta: { title: "" },
        component: "image/index",
      },
      {
        meta: { title: "" },
        component: "shop/category/list",
      },
      {
        meta: { title: "" },
        component: "shop/sku/list",
      },
      {
        meta: { title: "" },
        component: "shop/type/list",
      },
      {
        meta: { title: "" },
        component: "shop/comment/list",
      },
      {
        meta: { title: "" },
        component: "order/invoice/list",
      },
      {
        meta: { title: "" },
        component: "order/after-sale/list",
      },
      {
        meta: { title: "" },
        component: "order/order/list",
      },
      {
        meta: { title: "" },
        component: "user/user-list/list",
      },
      {
        meta: { title: "" },
        component: "user/user-level/list",
      },
      {
        meta: { title: "" },
        component: "set/base/index",
      },
      {
        meta: { title: "" },
        component: "set/express/index",
      },
      {
        meta: { title: "" },
        component: "set/manager/index",
      },
      {
        meta: { title: "" },
        component: "set/payment/index",
      },
      {
        meta: { title: "404" },
        component: "error/404",
      },
    ],
  },
  {
    meta: { title: "" },
    component: "login/index",
  },
  {
    path: "*",
    redirect: { name: "index" },
  },
];

// 
let getRoutes = function () {
  // 
  createRoute(routes);
  return routes;
};

// 
function createRoute(arr) {
  for (let i = 0; i < arr.length; i++) {
    if (!arr[i].component) return;
    // index
    let val = getValue(arr[i].component);
    // name
    arr[i].name = arr[i].name || val.replace(/\//g, "_");
    // path
    arr[i].path = arr[i].path || `/${val}`;
    // component
    let componentFun = import(`../../views/${arr[i].component}.vue`);
    arr[i].component = () => componentFun;
    if (arr[i].children && arr[i].children.length > 0) {
      createRoute(arr[i].children);
    }
  }
}

// index
function getValue(str) {
  // str = login/index
  // /
  let index = str.lastIndexOf("/");
  // /
  let val = str.substring(index + 1, str.length);
  // index
  if (val === "index") {
    return str.substring(index, -1);
  }
  return str;
}

export default getRoutes();
